.sidebar {
  display: flex;
  flex-direction: column;
  background-color: #0e1a35;
  width: 318px;
  height: 1352px;
  transition: width 0.5s ease-in-out;

  &_close {
    display: flex;
    flex-direction: column;
    height: 1352px;
    background-color: #0e1a35;
    width: 0px;
    transition: width 0.5s ease-in-out;
  }
}

