.imgLogo {
  width: 181px;
  height: 50px;
}
.icon {
  margin-right: 15px;
}

.option {
  cursor: pointer;
  display: inherit;
  align-items: center;
  visibility: visible;
  padding-left: 39px;
  height: 100px;
  border-top: 1px solid rgba(11, 10, 16, 0.3);
  font-size: 24px;
  color: white;
  text-decoration: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  transition: opacity 0.4s ease-in-out;
}

.option:hover {
  background-color: #122143;
  border-left: 10px solid #5584ff;
  padding-left: 29px;
}

.close {
  // visibility: hidden;
  opacity: 0;
  z-index: -1;
}
